import { useRef } from "react";
import { motion, useScroll, useTransform, useSpring } from "framer-motion";

const produtos = [
  {
    nome: "Camiseta Glitch",
    descricao: "Fragmentos de cdigo em movimento.",
    imagem: "https://i.ibb.co/fzMvfC1z/Camiseta.png",
    tamanho: "G",
    align: "left",
  },
  {
    nome: "Caneca",
    descricao: "Encapsulado no caos digital.",
    imagem: "https://i.ibb.co/JjTzxs8d/Caneca.png",
    tamanho: "M",
    align: "right",
  },
  {
    nome: "Bon Cdigo 404",
    descricao: "A identidade que no pode ser localizada.",
    imagem: "https://img.lojasrenner.com.br/item/929411503/original/6.jpg",
    tamanho: "P",
    align: "left",
  },
  {
    nome: "Jaqueta Kernel",
    descricao: "Ncleo quente, design frio.",
    imagem: "https://img.lojasrenner.com.br/item/928461322/original/3.jpg",
    tamanho: "G",
    align: "right",
  },
  {
    nome: "Cala Bytecode",
    descricao: "Executando estilo em alto nvel.",
    imagem: "https://img.lojasrenner.com.br/item/929395161/original/3.jpg",
    tamanho: "M",
    align: "left",
  },
  {
    nome: "Tnis Syntax",
    descricao: "Pise no erro, avance no cdigo.",
    imagem: "https://img.lojasrenner.com.br/item/804067930/small/3.jpg",
    tamanho: "P",
    align: "right",
  },
  {
    nome: "Relgio Loop Infinito",
    descricao: "O tempo  uma iterao elegante.",
    imagem: "https://img.lojasrenner.com.br/item/928812671/original/01.jpg",
    tamanho: "G",
    align: "left",
  },
];

const tamanhoClasses: Record<string, string> = {
  P: "w-full max-w-xs sm:max-w-sm md:max-w-md",
  M: "w-full max-w-sm sm:max-w-md md:max-w-lg",
  G: "w-full max-w-md sm:max-w-lg md:max-w-2xl lg:max-w-3xl object-contain",
};

const margemLateral = 100;
const backgroundUrl = "https://i.ibb.co/W4PfJQK4/Background-Landing-Page.jpg";

export default function LandingPage() {
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start end", "end start"],
  });

  const opacity = useSpring(
    useTransform(scrollYProgress, [0, 0.3, 0.7, 1], [0, 1, 1, 0]),
    { stiffness: 50, damping: 20 }
  );
  const scale = useSpring(
    useTransform(scrollYProgress, [0, 0.5, 1], [0.95, 1, 1.1]),
    { stiffness: 50, damping: 20 }
  );
  const y = useSpring(
    useTransform(scrollYProgress, [0, 0.5, 1], [100, 0, -100]),
    { stiffness: 50, damping: 20 }
  );

  return (
    <div
      className="overflow-y-scroll text-white font-mono space-y-36 flex flex-col items-start min-h-screen glitch-rgb"
      style={{
        paddingLeft: margemLateral,
        paddingRight: margemLateral,
        backgroundImage: `url(${backgroundUrl})`,
        backgroundSize: "cover",
        backgroundAttachment: "fixed",
        backgroundPosition: "top center",
      }}
    >
      <motion.section
        key="header"
        ref={ref}
        style={{ opacity, scale, y, width: "100%" }}
        className="mb-24 space-y-6"
      >
        <div className="flex justify-center items-center w-full px-4">
          <img
            src="https://i.ibb.co/s9TdjXh6/logo-Title.png"
            alt="placeholder"
            style={{
              scale: "1.5",
              paddingTop: "calc(10vh * 1.8)",
              maxWidth: "60vw",
              minWidth: "60vw",
              maxHeight: "70vh",
            }}
            className="max-w-xs md:max-w-lg lg:max-w-3xl w-full h-auto object-contain mt-20"
          />
        </div>
      </motion.section>

      {produtos.map((produto) => {
        const ref = useRef(null);
        const { scrollYProgress } = useScroll({
          target: ref,
          offset: ["start end", "end start"],
        });

        const opacity = useSpring(
          useTransform(scrollYProgress, [0, 0.5, 1], [0, 1, 0]),
          { stiffness: 50, damping: 20 }
        );
        const scale = useSpring(
          useTransform(scrollYProgress, [0, 0.5, 1], [0.95, 1, 1.1]),
          { stiffness: 50, damping: 20 }
        );
        const y = useSpring(
          useTransform(scrollYProgress, [0, 0.5, 1], [100, 0, -100]),
          { stiffness: 50, damping: 20 }
        );
        const flexDirection =
          produto.align === "right" ? "md:flex-row-reverse" : "md:flex-row";
        const alignClass = produto.align === "right" ? "ml-auto" : "mr-auto";

        return (
          <motion.section
            key={produto.nome}
            ref={ref}
            style={{ opacity, scale, y }}
            className={`flex flex-col ${flexDirection} ${alignClass} ${
              produto.tamanho === "G" ? "gap-6" : "gap-10"
            } justify-center`}
          >
            <img
              src={produto.imagem}
              alt={produto.nome}
              className={`${
                tamanhoClasses[produto.tamanho]
              } drop-shadow-lg w-full`}
            />
            <div
              style={{ display: "table" }}
              className={`text-left ${
                produto.tamanho === "G" ? "max-w-lg" : "max-w-xl"
              }`}
            >
              <div style={{ display: "table-cell", verticalAlign: "middle" }}>
                <h2 className="text-4xl font-bold mb-3">{produto.nome}</h2>
                <p className="text-lg text-gray-300 leading-relaxed">
                  {produto.descricao}
                </p>
              </div>
            </div>
          </motion.section>
        );
      })}
    </div>
  );
}
